@import "./reset.css";
 
:root {
 --app-dark-bg: #1a1d28;
 --app-red: #ff3c57;
 --app-green: #33ab4e;
 --app-purple: #833cff;
 --app-orange: #ff833c;
 --app-blue: #3c57ff;
 --app-blue-hover: #364ee6;
}

#root {
    width: 100%;
}

body {
    display: flex;
    align-items: center;
    flex-direction: column;
    background: radial-gradient(circle, #23262b 10%, transparent 11%),radial-gradient(circle at bottom left, #23262b 5%, transparent 6%),radial-gradient(circle at bottom right, #23262b 5%, transparent 6%),radial-gradient(circle at top left, #23262b 5%, transparent 6%),radial-gradient(circle at top right, #23262b 5%, transparent 6%);
    background-size: 3em 3em;
    background-color: #1A1D28;
    opacity: 1
}

main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #fff;
    font-size: 20px;
}

section {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;
}

form {
    width: 100%;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

input[type=text] {
    width: 20%;
    height: 48px;
    text-align: center;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    border-right: 1px solid rgba(255, 255, 255, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    background-color: rgba(255, 255, 255, 0.1);
    border-radius:4px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 26px 30px -10px, rgba(0, 0, 0, 0.1) 0px 16px 10px -10px;
    font-size: 16px;
    font-weight: 400;
    color: #fff;
    margin: 0 10px 0 10px;
}

input[type=submit] {
    width: 6%;
    height: 48px;
    text-align: center;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    border-right: 1px solid rgba(255, 255, 255, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    background-color: #33ab4e;
    border-radius:4px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 26px 30px -10px, rgba(0, 0, 0, 0.1) 0px 16px 10px -10px;
    font-size: 12px;
    font-weight: 400;
    color: #fff;
    margin: 0 10px 0 10px;
}

.taskBoard {
    width: 80%;
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
}


.taskBoard .task {
    position: relative;
    width: 30%;
    height: 100px;
    padding: 10px;
    color: #fff;
    margin-left: 10px;
    margin-top: 10px;
    border-radius:4px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 26px 30px -10px, rgba(0, 0, 0, 0.1) 0px 16px 10px -10px;
    border-left: 5px solid #33ab4e;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    border-right: 1px solid rgba(255, 255, 255, 0.1);
    background-color: rgba(255, 255, 255, 0.1);
}

.task .icon {
    position: absolute;
    display: flex;
    bottom: 0;
    right: 10px;  
}

.icon span {
    margin-right: 20px;
}

.icon span:hover {
    color: #ff3c57;
    cursor: pointer;
}

.app-container {
    padding: 0 160px 0 160px;
} 

header {
    display: flex;
    padding: 0 160px 0 160px;
}

nav {
    display: flex;
    justify-content: center;
    align-items: center;
}

ul {
    list-style-type: none;
}

nav ul {
    display: flex;
    color: #fff;
    list-style-type: none;
}

nav ul li a {
    font-size: 20px;
    color: #fff;
    text-decoration: none;
}

nav ul li a:hover {
    background: #ff833c;
}

nav ul li + li {
    margin-left: 30px;
    
}

.home-container {
    color: #fff;
    font-size: 20px;
}
